.search {
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 4rem;
        @include xs {
            grid-template-columns: 1fr;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
        @include md {
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__content {
        padding: 2rem 0 4rem;
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
        @include xs {
            padding-top: 0.8rem;
            padding-bottom: 2rem;
        }
    }
    &__row {
        display: flex;
        gap: 1.2rem;
        justify-content: center;
        @include xs {
            gap: 0.8rem;
        }
    }
    &__input {
        @extend %site-input;
        flex: 0 0 40rem;
        &::placeholder {
            color: var(--color-main-600);
            @include xs {
                transform:translate3d(0, -10%,0);
            }
        }
        @include xs {
            flex: 1;
        }
    }
    &__btn-submit {
        @extend %btn-base;
        @include xs {
            width: max-content;
        }
    }
    &__result {
        @extend %body-text;
        text-align: center;
        strong {
            padding: 0 0.8rem;
        }
    }
}