.course-list {
    $self: &;
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    &__info {
        @extend %post-info;
    }
    &__row {
        @extend %post-row;
    }
    &__btn-more {
        @extend %btn-lg;
    }
    &__course {
        display: grid;
        grid-template-columns: 4.8rem 1fr min-content max-content max-content max-content;
        align-items: center;
        gap: 2rem;
        background-color: var(--color-main-200);
        padding: 2rem;
        border-radius: 1.2rem;
        position: relative;
        &.expired {
            opacity: 0.75;
        }
        @include xs {
            gap: 1.2rem;
            grid-template: 
            "a b b b b b" min-content
            "c c c c c c" min-content
            "e e e e e e" min-content
            "f f f f f f" min-content
            "g g g g g g" min-content / 4.8rem 1fr 1fr 1fr 1fr 1fr;
        }
        @include sm {
            gap: 1.2rem;
            grid-template: 
            "a b b b b b" min-content
            "c c c c c c" min-content
            "e e e f f f" min-content
            "g g g g g g" min-content / 4.8rem 1fr 1fr 1fr 1fr 1fr;
        }
        @include md {
            gap: 1.2rem;
            grid-template: 
            "a b b b b b c" min-content
            "d e e f f g g" min-content / 4.8rem 1fr 1fr 1fr 1fr 1fr 1fr;
        }
        @include lg {
            gap: 1.2rem;
            grid-template: 
                "a b b b b b c" min-content
                "d e e f f g g" min-content / 4.8rem 1fr 1fr 1fr 1fr 1fr 1fr;
        }
        @include xl {
            grid-template: 
                "a b b c" min-content
                "d e f g" min-content / 4.8rem 1fr 1fr;
        }
    }
    &__teacher-image {
        @extend %couse-teacher-image;
        @include xs {
            grid-area: a;
        }
        @include sm {
            grid-area: a;
        }
        @include md {
            grid-area: a;
        }
        @include lg {
            grid-area: a;
        }
        @include xl {
            grid-area: a;
        }
    }
    &__course-title {
        @extend %main-post-title;
        @include xs {
            grid-area: c;
        }
        @include sm {
            grid-area: c;
        }
        @include md {
            grid-area: b;
        }
        @include lg {
            grid-area: b;
        }
        @include xl {
            grid-area: b;
        }
    }
    &__label {
        @extend %sm-label;
        @include xs {
            grid-area: b;
            margin-left: auto;
            margin-right: 0;
        }
        @include sm {
            grid-area: b;
            margin-left: auto;
            margin-right: 0;
        }
        @include md {
            position: absolute;
            right: 2rem;
            top: 2rem;
        }
        @include lg {
            position: absolute;
            right: 2rem;
            top: 2rem;
        }
        @include xl {
            grid-area: c;
            margin-right: 0;
            margin-left: auto;
        }
    }
    &__date{   
        @extend %course-date;
        @include xs {
            grid-area: e;
        }
        @include sm {
            grid-area: e;
        }
        @include md {
            grid-area: e;
        }
        @include lg {
            grid-area: e;
        }
        @include xl {
            grid-area: e;
        }
    }
    &__course-price {
        @extend %course-price;
        @include xs {
            grid-area: f;
        }
        @include sm {
            grid-area: f;
        }
        @include md {
            grid-area: f;
        }
        @include lg {
            grid-area: f;
        }
        @include xl {
            grid-area: f;
        }
    }
    &__control {
        display: flex;
        gap: 0.8rem;
        @include xs {
            grid-area: g;
        }
        @include sm {
            grid-area: g;
        }
        @include md {
            grid-area: g;
            justify-content: flex-end;
        }
        @include lg {
            grid-area: g;
            justify-content: flex-end;
        }
        @include xl {
            grid-area: g;
            justify-content: flex-end;
        }
    }
    &__course-all {
        display: grid;
        grid-template-columns: 1fr;
        gap: 6.4rem;
    }
    &__block {
        &.purchasing {
            background-color: #fff;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 2rem;
            border: dashed 0.1rem var(--color-danger-500);
            padding: 2rem;
            border-radius: 1.2rem;
        }
        & > h3 {   
            font-size: var(--font-size-xl);
            color: var(--color-main-700);
            margin-bottom: 2rem;
        }
    }
    &__btn-intro,
    &__btn-apply {
        @extend %btn-base;
    }
    &__btn-apply {
        &.active {
            background-color: var(--color-danger-500);
            border-color: var(--color-danger-500);
        }
    }
    &__pending,
    &__price {
        color: var(--color-main-800);
        font-size: var(--font-size-base);
        strong {
            padding: 0 0.2rem;
            color: var(--color-danger-500);
            font-size: var(--font-size-2xl);
        }
    }
    &__btn-purchasing {
        @extend %btn-base;
        width: min-content;
        display: block;
    }
}