.tammie-wrap {
    &__inner {
        @extend %container;
        display: grid;
        grid-template-columns: 1fr 40rem;
        gap: 8rem;
        @include xs {
            grid-template-columns: 1fr;
            gap: 5.6rem;
        }
        @include sm {
            grid-template-columns: 1fr;
            gap: 5.6rem;
        }
        @include md {
            grid-template-columns: 1fr;
            background-color: blue;
            gap: 5.6rem;
        }
        @include lg {
            grid-template-columns: 1fr 28rem;
            background-color: blue;
            gap: 5.6rem;
        }
        @include xl {
            grid-template-columns: 1fr 28rem;
        }
    }
}

.tammie-intro {
    $self: &;
    @include xs {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__inner-block {
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
    }
    &__wrap {
        display: flex;
        flex-direction: column;
        gap: 4rem;
        p {
            @extend %body-text;
        }
        @include xs {
            gap: 2rem;
        }
        ul {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0.8rem;
            li {
                font-size: var(--font-size-base);
                color: var(--color-main-700);
                line-height: 1.6;
                &:not(:last-child) {
                    margin-bottom: 0.4rem;
                }
                @include xs {
                    font-size: var(--font-size-xs);
                }
                @include sm {
                    font-size: var(--font-size-sm);
                }
            }
        }
    }
}