.about-pet-pulse {
    $self: &;
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__content {
        max-width: 96rem;
        display: flex;
        flex-direction: column;
        gap: 2rem;
        p {
            @extend %body-text;
            strong {
                display: block;
                padding: 0.8rem 0;
            }
        }
    }
}