.tammie-hero {
    background-color: var(--color-main-800);
    background-size: 72rem auto;
    height: 86rem;
    position: relative;
    &::after {
        position: absolute;
        width: 50%;
        height: 100%;
        left: 50%;
        transform: translate3d(-50%,0,0);
        z-index: 0;
        content: "";
        top: 0;
        background: transparent url('../images/slogan-kr.png') no-repeat center center;
        background-size: 90% auto;
        opacity: 0.1;
        @include xs {
            display: none;
        }
    }
    @include xs {
        height: 72rem;
    }
    @include sm {
        height: 80rem;
    }
    @include md {
        height: 56rem;
    }
    &__inner {
        @extend %container;
        position: relative;
        height: 100%;
        display: flex;
        align-items: center;
        z-index: 10;
        @include xs {
            align-items: flex-start;
            padding: 0 2.4rem;
        }
        @include sm {
            align-items: flex-start;
            padding: 0 2.4rem;
        }
    }
    &__name {
        font-size: var(--font-size-6xl);
        color: var(--color-main-200);
        line-height: 1.2;
        @include xs {
            font-size: var(--font-size-3xl);
        }
        @include sm {
            font-size: var(--font-size-3xl);
        }
        @include md {
            font-size: var(--font-size-4xl);
        }
    }
    &__name-en {
        font-size: var(--font-size-6xl);
        color: var(--color-main-200);
        line-height: 1.2;
        @include xs {
            font-size: var(--font-size-3xl);
        }
        @include sm {
            font-size: var(--font-size-3xl);
        }
        @include md {
            font-size: var(--font-size-4xl);
        }
    }
    &__profile {
        @include xs {
            padding: 4rem 0 0;
        }
        @include sm {
            padding: 4rem 0 0;
        }
    }
    &__img {
        width: 54rem;
        position: absolute;
        bottom: 0;
        right: 0;
        overflow: hidden;
        @include xs {
            width: 90vw;
            right: inherit;
            left: 50%;
            transform: translate3d(-50%,0,0);
            padding-top: 4rem;
        }
        @include sm {
            width: 90vw;
            background-color: var(--color-main-800);
            right: inherit;
            left: 50%;
            transform: translate3d(-50%,0,0);
            padding-top: 4rem;
        }
        @include md {
            width: 36rem;
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0;
            @include xs {
                width: 28rem;
                margin: 0 auto;
            }
            @include sm {
                width: 32rem;
                margin: 0 auto;
            }
        }
    }
    &__experience {
        padding-top: 2.4rem;
        @include xs {
            padding-top: 1.6rem;
        }
        @include sm {
            padding-top: 1.6rem;
        }
        li {
            font-size: var(--font-size-base);
            color: var(--color-main-200);
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
            @include xs {
                font-size: var(--font-size-xs);
            }
            @include sm {
                font-size: var(--font-size-sm);
            }
        }
    }
}