.tammie-book {
    $self: &;
    @include xs {
        padding-left: 2.4rem;
        padding-right: 2.4rem;
    }
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 5.6rem;
        @include xs {
            grid-template-columns: 1fr;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
        @include md {
            gap: 2.4rem;
            grid-template-columns: repeat(2, 1fr);
        }
        @include lg {
            gap: 3.2rem;
            grid-template-columns: repeat(2, 1fr);
        }
        @include xl {
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__item {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: min-content;
        gap: 1.2rem;
        @include xs {
            grid-template-columns: 1fr;
        }
    }
    &__book {
        margin: 0 auto;
        height: min-content;
        @include xs {
            width: 24rem;
        }
        a {
            display: block;
            margin: 0;
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        gap: 1.6rem;
    }
    &__book-title {
        a {
            font-size: var(--font-size-lg);
            color: var(--color-main-800);
            line-height: 1.6;
        }
    }
    &__book-description {
        @extend %sub-body-text;
    }
    &__detail {
        display: grid;
        grid-template-columns: 1fr;
    }
    &__specification {
        padding: 0.8rem 0; 
        display: grid;
        grid-template-columns: 1fr;
        gap: 0.5rem;
        &:not(:last-child) {
            border-bottom: solid 0.1rem var(--color-main-300);
        }
        span {
            &:nth-child(1) {
                font-size: var(--font-size-sm);
                color: var(--color-main-600);
                display: block;
                line-height: 1.6;
            }
            &:nth-child(2) {
                font-size: var(--font-size-base);
                color: var(--color-main-800);
            }
        }
    }
}