.forget-password {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--color-main-300);
    gap: 2rem;
    &__content {
        margin: 0 auto;
        width: min(calc(100% - 4rem), 40rem);
        border-radius: 1.2rem;
        padding: 4rem;
        position: relative;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        background-color: var(--color-main-100);
        @include xs {
            padding: 3.2rem 2.4rem;
        }
    }
    &__main-title {
        @extend %site-title;
    }
    &__form {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }
    &__row {
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        position: relative;
    }
    &__label {
        @extend %site-label;
    }
    &__input {
        @extend %site-input;
    }
    &__btn-submit {
        @extend %btn-lg;
        text-align: center;
    }
    &__text {
        @extend %sub-body-text;
        text-align: center;
    }
}