.membership {
    @extend %first-block;
    &__inner {
        @extend %container;
    }
    &__main-title {
        @extend %site-title;
    }
    &__level-list {
        display: grid;
        grid-template-columns: 1fr;
        @include xs {
            gap: 5.6rem;
        }
        @include sm {
            gap: 4rem;
        }
    }
    &__level {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        position: relative;
        &:not(:last-child) {
            border-bottom: solid 0.1rem var(--color-main-300);
        }
        @include xs {
            grid-template-columns: 1fr;
        }
        @include sm {
            grid-template-columns: 1fr;
        }
        @include md {
            grid-template-columns: 16rem repeat(4, 1fr);
        }
        @include lg {
            grid-template-columns: 16rem repeat(4, 1fr);
        }
        @include xl {
            grid-template-columns: 16rem repeat(4, 1fr);
        }
    }
    &__title {
        font-size: var(--font-size-base);
        text-align: center;
        color: var(--color-main-800);
        line-height: 1.6;
        width: 100%;
        font-weight: 900;
    }
    &__btn {
        @extend %btn-base;
        display: block;
        align-self: center;
    }
    &__level-image {
        width: 5.6rem;
        margin: 0 auto;
        img {
            max-width: 100%;
        }
        @include xs {
            width: 7.2rem;
        }
        @include sm {
            width: 7.2rem;
        }
        @include lg {
            width: 4rem;
        }
        @include xl {
            width: 4rem;
        }
    }
    &__row {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1.2rem;
        padding: 5.6rem 2.4rem;
        &:not(:last-child) {
            border-right: solid 0.1rem var(--color-main-200);
            @include xs {
                border-right: 0;
                border-bottom: solid 0.1rem var(--color-main-300);
            }
            @include sm {
                border-right: 0;
                border-bottom: solid 0.1rem var(--color-main-300);
            }
        }
        small {
            @extend %sm-label;
        }
        @include xs {
            padding: 2rem 0;
        }
        @include sm {
            padding: 2rem 0;
        }
        @include md {
            padding: 1.2rem;
        }
        @include lg {
            padding: 1.2rem;
        }
        @include xl {
            padding: 1.2rem;
        }
    }
    &__text {
        font-size: var(--font-size-sm);
        text-align: center;
        line-height: 1.6;
        color: var(--color-main-800);
    }
    &__remind-text {
        width: 100%;
        font-size: var(--font-size-sm);
        line-height: 1.6;
        color: var(--color-danger-700);
    }
    &__ul-lsit {
        list-style: disc;
        padding-left: 1.6rem;
        width: 100%;
        & > li {
            font-size: var(--font-size-base);
            line-height: 1.6;
            color: var(--color-main-800);
            font-weight: 700;
            @include md {
                font-size: var(--font-size-sm);
            }
            @include lg {
                font-size: var(--font-size-sm);
            }
            @include xl {
                font-size: var(--font-size-sm);
            }
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
        }
    }
    &__ol-lsit {
        padding-top: 0.4rem;
        & > li {
            font-size: var(--font-size-base);
            line-height: 1.6;
            color: var(--color-main-800);
            position: relative;
            padding-left: 1.6rem;
            font-weight: normal;
            @include md {
                font-size: var(--font-size-sm);
            }
            @include lg {
                font-size: var(--font-size-sm);
            }
            @include xl {
                font-size: var(--font-size-sm);
            }
            &::before {
                content: "";
                left: 0;
                position: absolute;
                width: 1.2rem;
                height: 1.2rem;
                background-color: var(--color-main-800);
                border-radius: 50%;
                top: 0;
                transform: translate3d(0,50%,0);
            }
            &::after {
                content: "";
                left: 0;
                position: absolute;
                width: 0.3rem;
                height: 0.4rem;
                border-right: solid 0.1rem #fff;
                border-bottom: solid 0.1rem #fff;
                top: 0;
                transform: translate3d(100%,170%,0) rotate(45deg);
            }
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
        }
    }
}